const GadgetsSpecification = styled.span`
color:#757575;font-size:10px;display:inline-block;padding:0 5px;vertical-align:middle;white-space:normal
`;

const Box = styled.table`
width:100%;border-spacing:0;padding-bottom:12px;white-space:normal;
  tr{
    td.leftImage{width:58px;
      .imgHolder{width:53px;height:115px;overflow:hidden;
        img{width:100%;height:100%;display:block;object-fit:cover;}
      }
    }
    td.laptop{width:106px;text-align:center;
      .imgHolder{width:100px;height:115px;overflow:hidden;
        img{width:100%;height:100%;display:block;object-fit:contain;}
      }
    }
    td.content{padding-left:4px;word-break:break-all;
      .model{color:#1C1C1C;font-size:13px;max-height:45px;overflow:hidden;}
      .price{color:#000000;font-size:10px;max-height:28px;overflow:hidden;
        span{color:#000000;font-size:14px;display:block;font-weight:bold;}
      }
      .features{color:#757575;font-size:10px;display:inline-block;padding:0 5px;vertical-align:middle;white-space:normal}
    }
    td.textBottom{vertical-align:bottom !important;padding-bottom:15px;padding-top:0px !important;}
  }
  &&:first-child td.content{vertical-align:top;padding-top:15px;}
  &&:last-child td.content{vertical-align:bottom;padding-bottom:18px;} 
`;

const ProductCard = styled(Box)`
  box-shadow:0px 0px 2px rgba(14, 31, 53, 0.12), 0px 1px 4px rgba(14, 31, 53, 0.06);border-radius:6px;padding:10px;margin-bottom:10px;border:2px solid transparent;
  tr{ 
    td{vertical-align:top;
      .compareIcon{width:12px;height:12px;margin:auto;display:inline-block;background-position:center center;background:url(${()=>imagePath("add_circle_outline_black.svg")}) no-repeat;background-size:12px auto;}
      .compare{color:#757575;font-size:10px;position:relative;top:-2px}
    }
    td.leftImage{
      width:53px;text-align:center;
    }
    td.details{ 
      padding-left:10px;
      h6{font-size:14px;color:#000;font-weight:700;margin-bottom:7px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2; 
        -webkit-box-orient: vertical;
      }
      p{font-size:14px;color:#000;margin-bottom:4px}
      ul{font-size:0px;padding-left:8px;line-height:20px;max-height:80px;overflow:hidden;margin-bottom:8px;
        li{font-size:13px;color:#757575;list-style:disc;list-style-position:inside;}
        li:last-child{margin-bottom:0}
      }
      span{color:#1F9EE1;font-size:10px;display:block;}
    }   
  }
  
  &&.selectedCard{
    border:2px solid #1F9EE1;box-shadow:0px 0px 2px rgba(14, 31, 53, 0.12), 0px 1px 4px rgba(14, 31, 53, 0.06);
    tr{
      td{
        .compareIcon{background:url(${()=>imagePath("add_circle_bkue_tick.svg")}) no-repeat}
        .compare{color:#1F9EE1}
      }
    }
  }
`;

const ProductCompare = styled.div`
  position:relative;text-align:center;margin-bottom:20px;
  margin:${props => props.margin};
  .singleProduct{
    .imgHolder{width:20px;height:43px;margin:0 auto 7px auto;border-radius:5px;background:#EEEEEE;
      img{width:100%;height:100%;display:block}
    }
    .productName{color:#000000;font-size:12px;padding:0 30px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;}
    .closeBtn{position:absolute;width:36px;height:36px;top:-12px;right:3px;background:url(${()=>imagePath("icon_close_btn.svg")}) center center / 24px 24px no-repeat;}
    &.DN{display:none}
    &.noContent{
      .imgHolder{margin-bottom:9px;
        img{display:none;}
      }
      .productName{background:#EEEEEE;width:62px;height:9px;border-radius:5px;display:block;margin:auto;font-size:0;}
      .closeBtn{display:none;}
    }
  }
  ul{padding:9px 34px 0;display:block;
    li{margin-bottom:4px;
      select{width:100%;height:26px;font-size:10px;color:#000000;padding:3px 0 3px 8px;border:1px solid #D8D8D8;border-radius:4px;background-color:transparent;}
      &:last-child{margin-bottom:0;}
    }
    &.DN{display:none}
  }
  &&.borderRight{background:url(${()=>imagePath("devider.svg")}) right center / auto no-repeat;}
`;

const ModelPrice = styled.p`
  color:#424242;font-size:16px;margin-bottom:8px;
`;

